  transition: all .3s ease-in-out;
  position: relative;
  box-shadow: 12px 12px 24px #edb60da1;
}

.button-anm:before {
  content: "";
  opacity: 0;
  border-radius: 1000px;
  transition: all .3s ease-in-out;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  box-shadow: 0 0 60px #edb60da1;
}

