.button-anm:after {
  content: "";
  width: 30px;
  height: 30px;
  border: 6px solid var(--color-accent);
  z-index: -1;
  border-radius: 100%;
  animation: 1.5s infinite ring;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

